import React from "react";
import PropTypes from "prop-types";

const DEFAULT_THEMES = ["light", "dark"];

const createThemeContext = () =>
  React.createContext({
    theme: "light",
    setTheme: () => {},
    toggleTheme: () => {},
    autoTheme: false,
    setAutoTheme: () => {},
    availableThemes: DEFAULT_THEMES,
    providerId: null,
  });

// Create a map to store multiple context instances
const ThemeContexts = new Map();

// Function to get or create a context for a specific provider ID
const getThemeContext = (providerId) => {
  if (!ThemeContexts.has(providerId)) {
    ThemeContexts.set(providerId, createThemeContext());
  }
  return ThemeContexts.get(providerId);
};

export const useTheme = (providerId = "default") => {
  const ThemeContext = getThemeContext(providerId);
  return React.useContext(ThemeContext);
};

export const ThemeProvider = ({
  children,
  defaultTheme = "light",
  storageKey = "lebify-theme",
  autoTheme: initialAutoTheme = false,
  morningHour = 6, // 6 AM
  eveningHour = 18, // 6 PM
  themes = DEFAULT_THEMES, // Array of available themes
  autoThemeMap = {
    // Map hours to themes for auto mode
    day: "light",
    night: "dark",
  },
  providerId = "default", // Add provider ID prop
}) => {
  const ThemeContext = getThemeContext(providerId);

  // Validate and merge themes
  const availableThemes = React.useMemo(() => {
    // Create a new array with all themes
    const allThemes = [...new Set([...themes])];

    // Ensure default themes are included if not present
    DEFAULT_THEMES.forEach((theme) => {
      if (!allThemes.includes(theme)) {
        allThemes.push(theme);
      }
    });

    // Ensure default theme is included
    if (!allThemes.includes(defaultTheme)) {
      console.warn(
        `Default theme "${defaultTheme}" not found in themes. Adding it.`
      );
      allThemes.push(defaultTheme);
    }

    // Ensure auto theme map themes are included
    if (!allThemes.includes(autoThemeMap.day)) {
      allThemes.push(autoThemeMap.day);
    }
    if (!allThemes.includes(autoThemeMap.night)) {
      allThemes.push(autoThemeMap.night);
    }

    return allThemes;
  }, [themes, defaultTheme, autoThemeMap]);

  // Initialize theme from localStorage or default
  const [theme, setTheme] = React.useState(() => {
    if (typeof window !== "undefined") {
      const savedTheme = localStorage.getItem(`${storageKey}-${providerId}`);
      return savedTheme && availableThemes.includes(savedTheme)
        ? savedTheme
        : defaultTheme;
    }
    return defaultTheme;
  });

  const [autoTheme, setAutoTheme] = React.useState(() => {
    if (typeof window !== "undefined") {
      const savedAutoTheme = localStorage.getItem(
        `${storageKey}-${providerId}-auto`
      );
      return savedAutoTheme ? JSON.parse(savedAutoTheme) : initialAutoTheme;
    }
    return initialAutoTheme;
  });

  // Function to determine theme based on time
  const getTimeBasedTheme = () => {
    const currentHour = new Date().getHours();
    return currentHour >= morningHour && currentHour < eveningHour
      ? autoThemeMap.day
      : autoThemeMap.night;
  };

  // Update theme based on time when autoTheme is enabled
  React.useEffect(() => {
    if (!autoTheme) return;

    const updateThemeByTime = () => {
      setTheme(getTimeBasedTheme());
    };

    // Update immediately
    updateThemeByTime();

    // Update every minute to check for time changes
    const interval = setInterval(updateThemeByTime, 60000);

    return () => clearInterval(interval);
  }, [autoTheme, morningHour, eveningHour]);

  // Save autoTheme preference
  React.useEffect(() => {
    if (typeof window !== "undefined") {
      localStorage.setItem(
        `${storageKey}-${providerId}-auto`,
        JSON.stringify(autoTheme)
      );
    }
  }, [autoTheme, storageKey, providerId]);

  // Update theme attributes and storage
  React.useEffect(() => {
    document.documentElement.setAttribute(`data-theme-${providerId}`, theme);
    document.body.setAttribute(`data-theme-${providerId}`, theme);

    if (typeof window !== "undefined" && !autoTheme) {
      localStorage.setItem(`${storageKey}-${providerId}`, theme);
    }
  }, [theme, storageKey, autoTheme, providerId]);

  // Toggle between light and dark themes
  const toggleTheme = () => {
    if (autoTheme) {
      setAutoTheme(false);
    }
    setTheme((prevTheme) => {
      const currentIndex = availableThemes.indexOf(prevTheme);
      const nextIndex = (currentIndex + 1) % availableThemes.length;
      return availableThemes[nextIndex];
    });
  };

  const contextValue = React.useMemo(
    () => ({
      theme,
      setTheme,
      toggleTheme,
      autoTheme,
      setAutoTheme,
      availableThemes,
      providerId,
    }),
    [theme, autoTheme, availableThemes, providerId]
  );

  return (
    <ThemeContext.Provider value={contextValue}>
      {children}
    </ThemeContext.Provider>
  );
};

ThemeProvider.propTypes = {
  children: PropTypes.node.isRequired,
  defaultTheme: PropTypes.string,
  storageKey: PropTypes.string,
  autoTheme: PropTypes.bool,
  morningHour: PropTypes.number,
  eveningHour: PropTypes.number,
  themes: PropTypes.arrayOf(PropTypes.string),
  autoThemeMap: PropTypes.shape({
    day: PropTypes.string,
    night: PropTypes.string,
  }),
  providerId: PropTypes.string,
};

export default ThemeProvider;
